<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a
          class="pagination__link pagination__link--arrow"
          :class="{'pagination__link--disabled':arrowDisabled==='prev'}"
          aria-label="Предыдущая страница"
          @click.prevent="prevPage"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li
        class="pagination__item"
        v-for="numberPage in pages"
        :key="numberPage"
    >
      <a
          class="pagination__link"
          :class="{'pagination__link--current':numberPage === page}"

          @click.prevent="paginate(numberPage)"
      >
        {{ numberPage }}
      </a>
    </li>


    <li class="pagination__item">
      <a
          class="pagination__link pagination__link--arrow"
          href="#"
          aria-label="Следующая страница"
          :class="{'pagination__link--disabled':arrowDisabled==='next'}"
          @click.prevent="nextPage()"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate'
  },
  name: "BasePagination",
  props: ['page', 'perPage', 'count'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage)
    },
    arrowDisabled() {
      if (this.page <= 1) {
        return 'prev'
      } else if (this.page >= this.pages) {
        return 'next'
      } else {
        return false
      }
    }

  },
  methods: {
    paginate(pageNumber) {
      this.$emit('paginate', pageNumber)
    },
    nextPage() {
      this.$emit('paginate', this.page + 1)
    },
    prevPage() {
      this.$emit('paginate', this.page - 1)
    }
  }
}
</script>

