<template>
  <li
      class="cart__item product"
  >
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x"
           alt="Название товара">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>
    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.product.price * item.amount | numberFormat }} ₽
    </b>

    <button
        @click.prevent="deleteItem"
        class="product__del button-del"
        type="button"
        aria-label="Удалить товар из корзины"

    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import {mapMutations, mapActions} from 'vuex'
import numberFormat from "@/helpers/numberFormat";

export default {
  props: ['item'],
  name: "CartItem",
  filters: {
    numberFormat
  },
  computed: {
    amount: {
      get() {
        return this.item.amount
      },
      set(val) {
        this.updateCartProductAmount({productId: this.item.product.id, amount: val})
      }
    }
  },
  methods: {
    ...mapActions(['updateCartProductAmount']),
    ...mapMutations(['deleteCartProduct']),
    deleteItem() {
      this.deleteCartProduct(this.item.productId)
    }
  }
}
</script>

