import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "@/config";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        cartProductsData: [],
        cartProducts: [],
        userAccessKey: null,
    },
    mutations: {
        updateCartProductAmount(state, {productId, amount}) {
            const item = state.cartProducts.find(el => el.productId === productId)

            if (item) {
                item.amount = amount
            }
        },
        updateCartProductsData(state, payload) {
            state.cartProductsData = payload
        },
        syncCartProduct(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            })
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId)
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey
        }
    },
    actions: {
        addProductToCart({state, commit}, {productId, amount}) {
            return axios
                .post(
                    API_BASE_URL + 'baskets/products',
                    {
                        productId,
                        "quantity": amount
                    },
                    {
                        params: {
                            userAccessKey: state.userAccessKey
                        }
                    }
                )
                .then(res => {
                    commit('updateCartProductsData', res.data.items)
                    commit('syncCartProduct')
                })
        },
        updateCartProductAmount({state, commit}, {productId, amount}) {
            commit('updateCartProductAmount', {productId, amount})

            if (amount < 1) {
                return
            }

            return axios
                .put(
                    API_BASE_URL + 'baskets/products',
                    {
                        productId,
                        "quantity": amount
                    },
                    {
                        params: {
                            userAccessKey: state.userAccessKey
                        }
                    }
                )
                .then(res => {
                    commit('updateCartProductsData', res.data.items)
                })
                .catch(() => {
                    commit('syncCartProduct')
                })
        },
        loadCart({commit, state}) {
            return axios.get(API_BASE_URL + 'baskets', {
                params: {
                    userAccessKey: state.userAccessKey
                }
            })
                .then(res => {
                    if (!state.userAccessKey) {
                        localStorage.setItem('userAccessKey', res.data.user.accessKey)
                        commit('updateUserAccessKey', res.data.user.accessKey)
                    }
                    commit('updateCartProductsData', res.data.items)
                    commit('syncCartProduct')
                })
        }
    },
    getters: {
        cartDetailProducts(state) {
            return state.cartProducts.map(el => {
                const product = state.cartProductsData.find(i => i.product.id === el.productId).product
                return {
                    ...el,
                    product: {
                        ...product,
                        img: product.image.file.url
                    }
                }
            })
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (+item.product.price * item.amount) + acc, 0)
        }
    }
})